import request from '@/request/api'
/**===========基本信息接口 */

// 获取列表
export function getListApi(data = {}) {
    return request({
        url: '/workorder/DemandappAction/queryList.htm',
        method: 'post',
        data: data
    })
}

//详情
export function getDetailsApi(id = "") {
    return request({
        url: '/workorder/DemandappAction/detail.htm',
        method: 'post',
        params:{ id: id }
    })
}

// 删除
export function deleteDemandapp(id = "") {
    return request({
        url: '/workorder/DemandappAction/delete.htm',
        method: 'post',
        params: { id: id }
    })
}

//新建页面
export function addDemandapp(data = {}) {
    return request({
        url: '/workorder/DemandappAction/addDemandapp.htm',
        method: 'post',
        data: data
    })
}

//审核前保存表单（保存草稿）
export function updateDemandapp(data = {}) {
    return request({
        url: '/workorder/DemandappAction/updateDemandapp.htm',
        method: 'post',
        data: data
    })
}



